body {
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
}

.favorites {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px;
}

.my-favorite {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-gap: 10px;
    align-items: center;
}

.my-favorite h1 {
    font-size: 40px;
    margin-bottom: 0;
    margin-top: 50px;
    position: relative;
    top: 100%;
}

.my-favorite p {
    font-size: 25px;
    margin: 0;
    margin-bottom: 50px;
    grid-column: 1 / span 1;
}

.fav-animation {
    width: 300px;
    height: 300px;
    margin: 0;
    position: relative;
    bottom: 20%;
}

.no-fav-animation {
    width: 500px;
    height: 400px;
    margin: 0 auto;
    padding-bottom: 80px;
}

.no-fav-text {
    margin: 0 auto;
    text-align: center;
    font-size: 25px;
    padding-top: 0px;
}

.favorite-section {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 40px;
}


.favorite-section ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.favorite-section li {
    margin: 10px;
}

.favorite-section .book-favorites {
    position: relative;
    width: 350px;
    height: 450px;
    background-color: #0000009c !important;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 1px 10px 20px #333333d7;
    transition: transform 0.5s ease;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
    margin-top: 30px;
    margin-bottom: 10px;
}

.favorite-section .book-favorites:hover {
    background-color: rgb(22, 22, 22);
    box-shadow: 1px 15px 20px rgba(102, 39, 190, 0.699), inset 0px 0px 10px rgba(59, 197, 212, 0.493);
    transform: scale(1.05);
}

.favorite-section .book-favorites h2 {
    font-size: 20px;
    font-weight: bold;
    color: rgba(59, 197, 212, 0.911) !important;
    text-align: center;
    margin-top: 5px;
    margin-bottom: 70px;
    max-height: 40px;
}

.favorite-section .book-favorites p {
    font-size: 14px;
    color: #DAFFFB !important;
    text-align: center;
}

.favorite-section .book-favorites img {
    width: 180px;
    height: 220px;
    object-fit: fill;
    border-radius: 4px;
    margin-bottom: 50px;
    transition: transform 0.5s ease;
}

.favorite-section .book-favorites img:hover {
    transform: scale(1.1);
}

.favorite-section .book-favorites .imageFav {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 200px;
    background-color: #f1f1f1;
    border-radius: 4px;
    color: #4d4d4d;
}

.favorite-section .book-favorites .imageFav p {
    margin-bottom: 5px;
}

.favorite-section .book-favorites button {
    background-color: #a81416d3;
    color: #DAFFFB;
    border: none;
    padding: 8px 12px;
    border-radius: 30px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    position: absolute;
    bottom: 10px;
    right: 10px;
}

.favorite-section .book-favorites button:hover {
    background-color: #b95456af;
}

.favorite-section h1 {
    font-size: 28px;
    margin-top: 0;
    color: #333;
    text-align: center;
    position: absolute;
    top: 20px;
    left: 0;
    right: 0;
}



.favorite-info {
    margin: 0 auto;
    padding-top: 70px;
    padding-bottom: 70px;
}



.most-searched-books {
    margin-top: 30px;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 15px;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.most-searched-books h2 {
    font-size: 24px;
    margin-bottom: 10px;
}

.most-searched-books .book-list {
    list-style-type: none;
    padding: 0;
}

.most-searched-books .book-list li {
    margin-bottom: 15px;
}

.most-searched-books .book-list a {
    color: #333;
    text-decoration: none;
}

.most-searched-books .book-list a:hover {
    color: #555;
}

.most-searched-books .book-card {
    background-color: #f1f1f1;
    padding: 10px;
    border-radius: 4px;
    height: 150px;
    width: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.most-searched-books .book-card h3 {
    font-size: 18px;
    margin-bottom: 5px;
}

.most-searched-books .book-card p {
    font-size: 14px;
    color: #777;
}


@media (max-width: 768px) {

    .my-favorite {
        display: block;
        width: 100%;
        margin: 0 auto;
        padding: 20px;
        text-align: center;
    }
    .my-favorite h1 {
        font-size: 25px;

    }
    .favorite-info {
        width: 100%;
        margin: 0 auto;
        text-align: center;
    }
    .no-fav-text {
        width: 100%;
        padding: 20px;
        font-size: 20px;
    }
    .no-fav-animation {
        width: 100%;
        padding: 20px;
        margin: 0 auto;
    }
}