@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.4.0/css/all.min.css);
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');


body {
    font-family: 'Poppins', sans-serif;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.book-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    /* Adjust the minmax values as needed */
    align-items: center;
    justify-content: center;
    margin: 50px;
    margin-top: 100px;
    grid-gap: 40px;
}




.book-item {
    width: 80%;
    height: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    margin-bottom: 50px;
    background-color: #000000b4;
    border-radius: 5px;
    box-shadow: 1px 8px 9px #333333b2;
    cursor: pointer;
    transition: background-color 0.5s ease;
    text-decoration: none;
    overflow: hidden;
    transition: transform 0.3s ease;
    opacity: 0;
    animation: fadeIn 0.5s ease-in-out forwards;
    animation-delay: calc(var(--index) * 0.1s);
    position: relative;

}


.book-item:hover {
    background-color: rgb(22, 22, 22);
    box-shadow: 1px 15px 20px rgba(102, 39, 190, 0.699), inset 0px 0px 10px rgba(59, 197, 212, 0.493);
    transform: scale(1.05);
}


.book-item h2 {
    font-size: 20px;
    font-weight: bold;
    color: rgba(59, 197, 212, 0.911) !important;
    text-align: center !important;
    padding: 0;
    margin: 20px;
    /* Adjust the margin-top as needed */
}


.book-item p {
    margin: 0 auto;
    color: #DAFFFB !important;
    margin: 20px;
    /* Adjust the margin-bottom as needed */
}

.book-item img {
    max-width: 100%;
    max-height: 250px;
    object-fit: contain;
    margin-bottom: 10px;
    /* Adjust the margin-bottom as needed */
    border-radius: 5px;
}

.book-list .imageErr {
    width: 150px;
    height: 200px;
    border: 2px solid rgb(85, 85, 85);
    border-radius: 5px;
    color: rgb(85, 85, 85) !important;
}

.book-list .imageErr p {
    color: rgb(85, 85, 85) !important;
    margin-top: 60px;
    font-size: 14px;
}

.book-list .imageErr i {
    font-size: 40px;
    margin: 0 auto;
}

.book-item .animate {
    opacity: 0;
    transform: translateY(30px);
    transition: opacity 6s ease, transform 6s ease;
}







@keyframes fadeIn {
    to {
        opacity: 1;
    }
}