@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.4.0/css/all.min.css);

.book-details {
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 7fr;
    grid-template-rows: auto auto 1fr auto;
    gap: 15px;
    padding-right: 50px;
    padding-left: 50px;
    padding-top: 50px;
    padding-bottom: 50px;
    border: 2px solid rgba(59, 197, 212, 0.911);
    background-color: #6627beb2;
    color: #DAFFFB;
    position: relative;
}

.book-details h2,
.book-details img,
.book-details button {
    border: 1px solid rgb(22, 22, 22);
    padding: 40px;
    background-color: #24262b;
    border-radius: 3px;
}

.book-details h2 {
    grid-column: 1 / 3;
    text-align: center;
    font-size: 25px;
    color: rgba(20, 215, 236, 0.911);
}

.description-p i::before,
.description-p i::after {
    content: '"';
}

.book-details i {
    padding-left: 5px;
    padding-right: 5px;
    font-style: italic;
    color: rgba(20, 215, 236, 0.911);
}

.book-details img {
    width: 100%;
    height: auto;
    grid-column: 1;
    grid-row: 2 / 4;
}

.book-details p {
    color: #DAFFFB;
    text-align: center;
    padding: 40px;
    background-color: #24262b;
    border-radius: 3px;
    text-align: center;
}

.book-details .description-p {
    grid-column: 2;
    grid-row: 2 / 6;
    font-size: 20px;
    padding: 40px;
    background-color: #24262b;
    border-radius: 3px;
    text-align: center;
    color: rgba(20, 215, 236, 0.911);
    border: 1px solid rgb(22, 22, 22);
}

.book-details .description-p span {
    color: #DAFFFB;
    border: none;
}

.book-details .download-button {
    grid-column: 1;
    grid-row: 5;
    padding: 20px;
}

.book-details .readNow-button {
    grid-column: 1;
    grid-row: 4;
    padding: 20px;
}

.book-details .fav-author {
    grid-column: 1 / 3;
    grid-row: 6;
    border: 1px solid rgb(22, 22, 22);
}

.book-details button {
    background-color: rgba(59, 197, 212, 0.911);
    color: #DAFFFB;
    cursor: pointer;
    transition: opacity 0.5s ease;
    width: 250px;
    height: 100px;
}

.book-details .favorites-button {
    grid-column: 1;
    grid-row: 2;
    background-color: transparent;
    border: none;
    width: auto;
    height: 20px;
    padding-top: 15px;
}

.book-details .favorites-button:hover {
    color: rgba(59, 197, 212, 0.911);
    transition: 0.4s;
}

.book-details button.added {
    opacity: 0;
    pointer-events: none;
    position: relative;
}

.book-details button.added.show {
    display: block;
    animation: fadeOut 0.5s ease-in-out forwards;
}

.book-details .button-container {
    grid-column: 1;
    grid-row: 2;
    text-align: center;
}

.book-details .checkIcon {
    text-align: center;
    margin-top: 5px;
}

.book-details .checkIcon.show {
    display: block;
    opacity: 1;
    transform: translateY(0);
}

@keyframes fadeOut {
    0% {
        opacity: 1;
        transform: translateY(0);
    }

    90% {
        opacity: 1;
        transform: translateY(-5px);
    }

    100% {
        opacity: 0;
        transform: translateY(-10px);
    }
}


@media (max-width: 768px) {
    .book-details {
        width: 100%;
        margin: 0 auto;
        padding: 20px;
        display: block;
    }

    .book-details h2 {
        font-size: 20px;
    }
    .book-details .fav-author {
        font-size: 14px;
    }

    .book-details img {
        margin: 0 auto;
        display: block;
    }

    .book-details .description-p {
        font-size: 16px;
        padding-bottom: 20px;
    }
    .book-details .description-p span p {
        padding: 20px 0px;
    }

    .book-details .download-button,
    .book-details .readNow-button,
    .book-details .fav-author {
        width: 100%;
        margin: 0 auto;
        text-align: center;
    }

    .book-details img,
    .book-details .description-p,
    .book-details .fav-author,
    .book-details .download-button {
        margin: 10px auto;
    }
}