.about-page {
    width: 100%;
    padding: 100px;
    margin: 0 auto;
    text-align: center;
}

.about-grid {
    margin: 0 auto;
    width: 60%;
    padding: 20px;
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(fill, minmax(300px, 1fr));
    grid-gap: 30px;
}

.about-grid p {
    font-size: 20px;
    margin-top: 10px;
    border: 3px solid #24262b;
    background-color: #24262b;
    border-radius: 3px;
    padding: 30px;
    color: whitesmoke;
}

.about-newsLetter {
    width: 100%;
    margin: 0 auto;
    text-align: center;
    align-items: center;
}

.subscription-form input {
    padding: 12px 20px;
    font-size: 16px;
    margin-right: 10px;
    margin-top: 10px;
    border: none;
    border-radius: 25px;
    background-color: #f1f1f1;
    color: #333;
    transition: background-color 0.4s ease, color 0.4s ease;
}


.subscription-form button {
    padding: 12px 20px;
    font-size: 16px;
    border: 1px solid #333;
    color: black;
    cursor: pointer;
    border-radius: 25px;
    transition: background-color 0.3s ease-in-out;
    outline: none;
    mix-blend-mode: screen;
    position: relative;
}
.subscription-form button::before {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    content: '';
    background: white;
    border-radius: 25px;
    mix-blend-mode: color-burn;
}
.subscription-form button:hover {
    background: #333;
    color: white;
}


@media (max-width: 768px) {
    .about-page {
        padding: 20px;
        margin-top: 20px;
    }

    .about-page h1,
    .about-page h2 {
        font-size: 25px;
    }

    .about-grid {
        width: 100%;
        grid-template-columns: 1fr;
        grid-gap: 20px;
    }

    .about-grid p {
        font-size: 16px;
    }

    .about-newsLetter {
        margin-top: 40px;
    }

    .subscription-form input,
    .subscription-form button {
        font-size: 16px;
        margin-top: 10px;
    }
}