.header {
    border: 2px solid rgba(59, 197, 212, 0.911);
    border-bottom-color: transparent;
    height: 140px;
    background-color: #24262b;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;

}

.header .logo {
    height: auto;
    width: 220px;
    display: flex;
    align-items: center;
}

.header .logo img {
    filter: drop-shadow(12px 12px 10px rgba(0, 0, 0, 0.7));
}

.nav-links ul {
    list-style: none;
}

.nav-links li {
    display: inline-block;
    position: relative;
}

.nav-links li a {
    width: 100%;
    display: flex;
    align-items: center;
    padding: 20px 25px;
    color: whitesmoke;
    text-decoration: none;
    text-align: center;
    font-size: 20px;
    font-weight: 600;
    transition: color 0.4s ease;
}

.nav-link {
    text-decoration: none;
    color: #64CCC5;
    font-weight: bold;
}

.nav-link:hover {
    color: rgba(59, 197, 212, 0.911);
}


/* Media query for screens smaller than 768px */
@media (max-width: 768px) {

    .header {
        width: 100%;
        margin: 0 auto;
    }

    .header .logo {
        width: 140px;
    }
    .nav-links ul {
        display: flex;
        margin-left: -20px;
    }
    .nav-links li a {
        font-size: 13px;
        padding: 13px;
    }
}