@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');


html {
    background: linear-gradient(rgba(59, 197, 212, 0.911), rgb(101, 39, 190), rgba(59, 197, 212, 0.911));
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: 100% 200%;
    background-position: top;
    scroll-behavior: smooth;
}

body {
    font-family: 'Poppins', sans-serif;
}

.home {
    align-items: center;
    text-align: center;
}

.firstH1 {
    margin-top: 120px;
    margin-bottom: 20px;
    color: rgb(22, 22, 22);
    font-size: 32px;
}

.lottie-div p {
    margin-top: 120px;
    margin-bottom: 20px;
    color: rgb(22, 22, 22);
    font-size: 22px;
}

.lottie-text h2 {
    margin-top: 120px;
    margin-bottom: 20px;
    color: rgb(22, 22, 22);
    text-align: start;
}

.home p {
    color: rgb(22, 22, 22);
}

.home .fa-regular .fa-bookmark {
    font-size: 40px;
    margin: 0 auto;
    padding-left: 30px;
}

.lottie-container {
    width: 350px;
    height: 350px;
    margin-right: 30px;
    margin-top: -50px;
    margin-bottom: 100px;
    display: inline-block;
}

.lottie-text {
    display: inline-block;
    vertical-align: top;
    margin-top: -30px;
    margin-right: 60px;
}

@media (max-width: 768px) {
    .home {
        margin-top: 20px;
        width: 100%;
        margin: 0 auto;
        padding: 20px;
    }

    .home i{
        display: none;
    }

    .firstH1,
    .lottie-div p,
    .lottie-text h2 {
        margin-top: 40px;
        font-size: 24px;
    }

    .lottie-container {
        width: 100%;
        max-width: 300px;
        margin: 0 auto;
    }

    .lottie-text {
        text-align: center;
        margin: 0 auto;
    }
}